.routeWrapper {
    display: flex;
    justify-content: center;
}

.route-locality {
    display: flex;
    flex-direction: column;
}

.reorder {
    display: flex;
    flex-direction: row;
}


.routes {
    position: relative;
    width: 100%;
    align-items: center;
    display: flex;
    flex-direction: column;
}

.route {
    cursor: grab;
    display: flex;
    align-items: center;
}

.plus,
.minus {
    align-self: center;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 18px;
    min-width: 18px;
    cursor: pointer;
    margin: 0 8px;
}

.routeArrow {
    display: flex;
    padding: 0 5px;
    align-items: center;
}