.notification {
    height: fit-content;
    display: block;
    font-size: 24px;
    background-color: red;
    color: whitesmoke;
    padding-right: 60px;
}

.confirm {
    position: absolute;
    top: 10px;
    right: 38px;
    border: solid;
    width: 60px;
    text-align: center;
    border-radius: 4px;
    height: inherit;
    font-size: 20px;
    cursor: pointer;
    z-index: 33;
}

@media screen and (max-width: 700px) {

    .confirm {
        right: 15px;
        top: 22px
    }

    .birthday {
        padding-bottom: 5px;
    }
}

.confirm:hover {
    color: rgb(225, 144, 144);
}

.birthday {
    margin-left: 50px;
    padding-top: 8px;
    min-height: 40px;
}

.dopog {
    background-color: rgb(77, 62, 62);
    color: whitesmoke;
    padding: 4px 25px;
    display: flex;
    justify-content: space-between;
}

.close-dopog {
    align-items: center;
    cursor: pointer;
}